import { Injectable } from '@angular/core';
import {Observable} from "rxjs/Observable";
import {Http, RequestOptions,Headers} from "@angular/http";

@Injectable()
export class CheckoutServiceService {

  constructor(
    private http: Http
  ) { }
  getShopingCart():Observable<any>{
    //("getShopingCart service ");
    let url = "http://localhost/slim-cart-master/public/cart";
    let headers = new Headers();
    let options = new RequestOptions({headers: headers, withCredentials: true});
    return this.http.get(url, options)
      .map((res) => {
        if(res){
         return res.json().cart;

        }

      });
  }

  updateQt(id:string,quantity:string):Observable<any>{
    //("getShopingCart service ");
    let url = "http://localhost/slim-cart-master/public/cart/update/"+id+"/"+quantity;
    let headers = new Headers();
    let options = new RequestOptions({headers: headers, withCredentials: true});
    return this.http.get(url, options)
      .map((res) => {
        if(res){
          return res.json();

        }

      });
  }


  removeItem(id:string):Observable<any>{
    //("getShopingCart service ");
    let url = "http://localhost/slim-cart-master/public/cart/remove/"+id;
    let headers = new Headers();
    let options = new RequestOptions({headers: headers, withCredentials: true});
    return this.http.get(url, options)
      .map((res) => {
        if(res){

          return res.json();

        }

      });
  }

  showOrder(hash:string):Observable<any>{
    //("test appel service");

    let url = "http://localhost/slim-cart-master/public/order/"+hash;
    let headers = new Headers();
    let options = new RequestOptions({headers: headers, withCredentials: true});
    return this.http.get(url, options)
      .map((res) => {
        if(res){
          if(res.json().erreur){
            //("erreur")
          }
          return res.json().order;


        }

      });
  }


}



