import { Injectable } from '@angular/core';
import {Observable} from "rxjs/Observable";
import {Http, RequestOptions,Headers} from "@angular/http";

@Injectable()
export class CatalogService {

  constructor(private http: Http) {
  }



  getAllCategories(): Observable<any> {
    let url = "http://localhost/slim-cart-master/public/cat";
    let headers = new Headers();
    let options = new RequestOptions({headers: headers, withCredentials: true});
    return this.http.get(url, options)
      .map((res) => {
        if(res){
          return res.json();
        }

      });
  }

  getListProducts(id: string): Observable<any> {

    let url = "http://localhost/slim-cart-master/public/listproducts/"+id;
    let headers = new Headers();
    let options = new RequestOptions({headers: headers, withCredentials: true});
    return this.http.get(url, options)
      .map((res) => {
      if(res){
        return res.json();
      }

      });
  }

  getDetailProduct(idProduct:any):Observable<any> {
    let url = "http://localhost/slim-cart-master/public/products/"+idProduct;
    let headers = new Headers();
    let options = new RequestOptions({headers: headers, withCredentials: true});
    return this.http.get(url, options)
      .map((res) => {
          return res.json();
      });


  }
  addToCart(idProduct:any,qt:any):Observable<any> {
    let url = "http://localhost/slim-cart-master/public/cart/add/" + idProduct + "/" + qt;
    let headers = new Headers();
    let options = new RequestOptions({headers: headers, withCredentials: true});
    return this.http.get(url, options)
      .map((res) => {
      return res.json();
      });
  }
}



